.chart {
    -ms-flex-preferred-size: 100%;
        flex-basis: 100%;
    display: -ms-flexbox;
    display: flex;
}

.chart-list {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    width: 100%;
}

.chart-list__item {
    padding: 1rem 1.5rem;
    border-bottom: 0.1875rem solid transparent;
    text-transform: uppercase;
    color: var(--white);
    display: block;
}

.chart-list__item:hover {
    background-image: -webkit-gradient(linear, left bottom, left top, from(rgb(33, 33, 33)), to(rgb(38, 38, 38)));
    background-image: -webkit-linear-gradient(bottom, rgb(33, 33, 33) 0%, rgb(38, 38, 38) 100%);
    background-image: -o-linear-gradient(bottom, rgb(33, 33, 33) 0%, rgb(38, 38, 38) 100%);
    background-image: linear-gradient(0deg, rgb(33, 33, 33) 0%, rgb(38, 38, 38) 100%);
    border-bottom: 0.1875rem solid var(--red);
}

.selected {
    background-image: -webkit-gradient(linear, left bottom, left top, from(rgb(33, 33, 33)), to(rgb(38, 38, 38)));
    background-image: -webkit-linear-gradient(bottom, rgb(33, 33, 33) 0%, rgb(38, 38, 38) 100%);
    background-image: -o-linear-gradient(bottom, rgb(33, 33, 33) 0%, rgb(38, 38, 38) 100%);
    background-image: linear-gradient(0deg, rgb(33, 33, 33) 0%, rgb(38, 38, 38) 100%);
    border-bottom: 0.1875rem solid var(--red);
}


